<template>
    <div>
        <el-form :inline="true" :model="conditions" class="demo-form-inline">   
            <el-form-item label="">
                <el-input v-model="conditions.case_name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="conditions.case_mobile" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="conditions.case_id_num" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="conditions.talk_recode" placeholder=""></el-input>
            </el-form-item>
            
             <el-form-item label="id">
                <el-input v-model="conditions.id" placeholder="id"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="conditions.case_status" placeholder="">
                    <el-option 
                        :label="item.label" 
                        :value="item.value"
                        v-for='item in caseStatusList'
                        :key="item.value"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="conditions.case_level" placeholder="">
                    <el-option 
                        v-for='item in levelList'
                        :key='item.id'
                        :label='item.name'
                        :value='item.id'
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="special" filterable :filter-method = 'filterFn' @change="filterFn" placeholder="">
                    <el-option
                        v-for="item in filterList"
                        :label="item.name" 
                        :value="item.id"
                        :key='item.id'>
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="conditions.case_money1" type='number' placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="-">
                <el-input  v-model="conditions.case_money2" type='number'  placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="conditions.talk_time1" type='number' placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="-">
                <el-input  v-model="conditions.talk_time2" type='number' placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-date-picker
                    v-model="conditions.case_date"
                    type="daterange"
                    range-separator=""
                    start-placeholder=""
                    end-placeholder="">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="">
                <el-date-picker
                    v-model="conditions.case_back_date"
                    type="daterange"
                    range-separator=""
                    start-placeholder=""
                    end-placeholder="">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="">
                 <el-select v-model="conditions.depart_id" @change="changeFn" placeholder="">
                    <el-option
                        v-for="item in departmentList"
                        :key="item.id"
                        :label="item.depart_name"
                        :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="conditions.staff_id" placeholder="">
                    <el-option
                        v-for="item in staffList"
                        :key="item.id"
                        :label="item.true_name"
                        :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="conditions.batch_id" placeholder="">
                    <el-option 
                        v-for="item in batchList"
                        :key='item'
                        :label="item" 
                        :value="item"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="conditions.case_color" placeholder="">
                    <el-option label="" value="1"></el-option>
                    <el-option label="" value="2"></el-option>
                    <el-option label="" value="3"></el-option>
                    <el-option label="" value="4"></el-option>
                    <el-option label="" value="5"></el-option>
                    <el-option label="" value="6"></el-option>
                    <el-option label="" value="7"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-date-picker
                    v-model="conditions.case_last_collection_date"
                    type="daterange"
                    range-separator=""
                    start-placeholder=""
                    end-placeholder="">
                </el-date-picker>
            </el-form-item>
            
        </el-form>
    </div>
</template>
<script>
import  { axiosRequest } from '@/assets/js/Yt.js'
export default {
    name:'formCaseFirst',
    props:[
        'conditions',
        'levelList',
        'batchList',
        'departmentList',
        'staffList',
        'caseStatusList',
    ],
    data(){
        return{
            filterList:[],
            special:''//
        }
    },
    methods:{
        changeFn(val){
            this.$emit('changeFn',val)
            this.$emit('getDepartmentList',2)
        },
        filterFn(val){
            let conf = {
                url : '/api/api_backend.php?r=collection/client-search',
                data:{
                    case_client:val
                },
                success:(data)=>{
					if( data.statusCode == 1 ){
                        this.filterList = data.info.client
                        // console.log(data.info)
                        this.$emit('changeCaseClient',data.info.client_batch_id)
                        // this.conditions.case_client = data.info.client_batch_id
                        
                    }else if(data.statusCode == 0){
                        this.filterList = []
                    }
                }
            }
            axiosRequest(conf)
        },
        // inputChange(val){
        //     let conf = {
        //         url : '/api/api_backend.php?r=collection/client-search',
        //         data:{
        //             case_client:val
        //         },
        //         success:(data)=>{
		// 			if( data.statusCode == 1 ){
		// 				this.filterList = data.info
        //             }else if(data.statusCode == 0){
        //                 this.filterList = []
        //             }
        //         }
        //     }
        //     axiosRequest(conf)
        // }
    }
}
</script>
<style>
.el-date-editor .el-range-separator,.el-date-editor .el-range__icon{line-height:24px;}
</style>




    


