<template>
	<section>
    <div class="CenterMain  MainHp">
      <div class="TopForm">
        <div class="CallCount">  
					<div class="AIadd" style="float:left">
							<el-button type="success" @click="addTpl">
							<i class="fa fa-plus"></i></el-button> 
					</div>
					<el-form :inline="true"  class="demo-form-inline" style="float:left;margin-left:10px" @submit.native.prevent>
						<el-form-item label=":">
							<el-input v-model="keyword" placeholder=""></el-input>
						</el-form-item>
						<el-form-item>
							<el-button type="primary" @click="onSubmit"></el-button>
						</el-form-item>
					</el-form>
					<div class="TableList">
							<el-table ref="multipleTable" :data="infos" style="width:100%" border  v-loading="loading" :height="parseInt(total)>0?500:100">
									<el-table-column type="index" label="" width="60" :index="index" fixed="left"></el-table-column>
									<el-table-column prop="name" label=""> </el-table-column>
									<el-table-column prop="sound_type" label=""></el-table-column>
									<el-table-column prop="create_time" label="" ></el-table-column>
									<el-table-column prop="sound_type" label=""  width="350">
											<template slot-scope="scope">
													<el-button type="primary" plain @click="sound(scope.row)"></el-button>
													<el-button type="primary" plain @click="asrSound(scope.row)"></el-button>
													<el-button type="primary" plain @click="tree(scope.$index,scope.row)"></el-button>
													<el-button type="primary" plain @click="edit(scope.$index,scope.row)"></el-button>
													<el-button type="danger" plain @click="del(scope.$index,scope.row)"></el-button>
											</template>
									</el-table-column>
							</el-table>
					</div>
					<page-change 
						:total="total"
						:page="page"
						v-on:pageSizeChange='pageSizeChangeFn($event)'
						v-on:currentPageChange='currentPageChangeFn($event)'
						v-show="total!=0"></page-change>
        </div>
      </div>
    </div>
        <!--  -->
        <div class="dial-header tag-dial">
          <el-dialog title="" :visible.sync="AddTpl" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="!saving" v-move>
              <el-form label-width="100px">
                  <el-form-item label="">
                      <el-input v-model="AddTplData.name" style="width: 100%"></el-input>
                  </el-form-item>
                  <el-form-item label=":">
                    <el-select v-model="AddTplData.sound_type"  placeholder="" >
                      <el-option :label="item.sound_type" :value="item.sound_type" v-for="(item,index) in AddData.sound_type" :key="index"></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label=":">
                    <el-select v-model="AddTplData.template_id" placeholder="">
                      <el-option label="" value="0"></el-option>
                      <el-option :label="item.tpl_name" :value="item.id" v-for="(item,index) in  AddData.select_tpl_id" disabled :key="index"></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label=":">
                    <el-select v-model="AddTplData.sound_id"  placeholder="" >
                      <el-option  :label="item.name" :value="item.id" v-for="(item,index) in  AddData.sound" :key="index"></el-option>
                    </el-select>
                    <el-button type="primary" @click="addSound"></el-button>
                  </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                  <el-button type="primary" @click="AddSave" v-show="!saving"></el-button>
                  <el-button type="primary" v-show="saving"></el-button>
                  <el-button @click="AddTpl = false"></el-button>  
              </div>
          </el-dialog>
        </div>
        <!--  -->
        <div class="dial-header tag-dial">
          <el-dialog title="" :visible.sync="AddSound" v-move>
              <el-form label-width="100px">
                  <el-form-item label="">
                      <el-input v-model="AddSoundData.name" style="width: 400px"></el-input>
                  </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                  <el-button type="primary" @click="AddSoundSave"></el-button>
                  <el-button @click="AddSound = false"></el-button>  
              </div>
          </el-dialog>
        </div>
        <!--  -->
        <div class="dial-header tag-dial">
          <el-dialog title="" :visible.sync="EditTpl" v-move>
              <el-form label-width="100px">
                  <el-form-item label="">
                      <el-input v-model="EditTplData.name" style="width: 450px"  ></el-input>
                  </el-form-item>
                  <el-form-item label=":">
                    <el-input v-model="EditTplData.sound_type" style="width: 450px"  :disabled="true"></el-input>
                  </el-form-item>
                  <el-form-item label=":">
                    <el-input v-model="EditTplData.sound_name" style="width: 450px"  :disabled="true"></el-input>
                  </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                  <el-button type="primary" @click="EditSave"></el-button>
                  <el-button @click="EditTpl = false"></el-button>  
              </div>
          </el-dialog>
        </div>
				</section>
    </template>

<script>
import {axiosRequest,clone,message} from '@/assets/js/Yt.js'
import pageChange from '@/components/pageChange.vue'
	export default {
		components:{
      'page-change':pageChange
    },
		data() {
			return {
				saving:false,
        page:1,
        total:0,
        page_size:10,
        loading:true,
        keyword:"",    //
        infos:[],
        AddTpl:false,     //
        AddData:{
          sound_type:[],    //
          select_tpl_id:[],     //
          sound:[]             //
        },
        select_tpl_id:"0",
        AddTplData:{       //
          name:"",
          sound_type:"",
          sound_id:"",
          template_id:"0"      //
        },
				AddTplData1:{       //
          name:"",
          sound_type:"",
          sound_id:"",
          template_id:"0"      //
        },
				dataClone:{},
        AddSound:false,     //
        AddSoundData:{ name:""},
        EditTpl:false,               //
        EditTplData:{
          name:"",
          sound_type:"",
          sound_name:"",
          template_id:""
        }
			}
		},
		mounted() {
			this.init()
		},
		methods: {
		 	index(val){
          return (this.page-1)*this.page_size+val+1
      },
			// 
			init(){
				const page = this.page;
				const page_size = this.page_size;
				const url = "/api/api_backend.php?r=template/template-list"
				const conf = {
					url,
					data:{
						page,
						page_size,
					},
					success:(data)=>{
						this.infos = data.info.info
						this.total = parseInt(data.info.total)
						this.loading = false
					}
				}
				axiosRequest(conf)
			},
				// 
			addListTpl(){
				const url = "/api/api_backend.php?r=template/template-list-all"
				const conf = {
					url,
					success:(data)=>{
						this.AddData.select_tpl_id = data.info.map((item)=>{
							return {
								id:item.id,
								tpl_name:item.name
							}
						})
					}
				}
				axiosRequest(conf)
			},
				//  
				addListSound(){
					const url = "/api/api_backend.php?r=template/sound-list-all"
					const conf = {
						url,
						success:(data)=>{
							this.AddData.sound = data.info
						}
					}
					axiosRequest(conf)
				},
				// 
				addTpl(){ 
					this.AddTpl = true
					this.AddData.sound_type = [{sound_type:""},{sound_type:""}]
					this.addListTpl()
					this.addListSound()
				},
				clone(obj){
					 return JSON.parse(JSON.stringify(obj))
				},	
				//  
				AddSave(){
					this.saving = true
					const data = this.AddTplData
					if( this.AddTplData.sound_type == ""){
						data.sound_type = 0
					}else if( this.AddTplData.sound_type == ""){
						data.sound_type = 1
					}
					const url = "/api/api_backend.php?r=template/template-add"
					const conf = {
						url,
						data:data,
						success:(data)=>{ 
							this.$alert(data.message)
							if(data.statusCode == 1){
								this.AddTpl = false
								this.init()		
								this.AddTplData = this.clone(this.AddTplData1)
							}else{
								this.AddTpl = true
							}
							this.saving = false
						}
					}
					axiosRequest(conf)
				},
				// 
				addSound(){
					this.AddSound = true
				},
				AddSoundSave(){
					const name = this.AddSoundData.name
					const url = "/api/api_backend.php?r=template/sound-add"
					const conf = {
						url,
						data:{name},
						success:(data)=>{ 
							this.$alert(data.message)
							if(data.statusCode == 1){
								this.AddSound = false
								this.addListSound()
							}else{
								this.AddSound = true
							}
						}
					}
					axiosRequest(conf)
				},
            // 
            edit(index,row){
              this.EditTpl = true
              const url = "/api/api_backend.php?r=template/template-info"
              const conf = {
                url,
                data:{template_id:row.id},
                success:(data)=>{
                  this.EditTplData = data.info
									this.EditTplData.template_id = data.info.id
                }
              }
              axiosRequest(conf)
            },
            EditSave(){
              var template_id = this.EditTplData.template_id
              const name = this.EditTplData.name
              const url = "/api/api_backend.php?r=template/template-edit"
              const conf = {
                url,
                data:{template_id,name},
                success:(data)=>{
                  if(data.statusCode == 1){
                    this.EditTpl = false
                    this.init()
                  }else{
                    this.EditTpl = true
                  }
                  this.$alert(data.message)
                }
              }
             axiosRequest(conf)
            },
            // 
            del(index,row){
              this.$confirm('?', '', {
                  confirmButtonText: '',
                  cancelButtonText: '',
                  type: 'warning'
                }).then(() => {
                  const template_id = row.id
                  const url = "/api/api_backend.php?r=template/template-del"
                  const conf = {
                    url,
                    data:{template_id},
                    success:(data)=>{
                      if(data.statusCode == 1){
                        this.init()
                      } 
                      this.$alert(data.message)
                    }
                  }
                axiosRequest(conf)
                }).catch(() => {
                  this.$message({
                    type: 'info',
                    message: ''
                  });          
                });
            },
            // 
            onSubmit(){
              const keywords = this.keyword
              const url = "/api/api_backend.php?r=template/template-search"
              const conf = {
                url,
                data:{keywords},
                success:(data)=>{
									this.total = 0
                  if(data.statusCode == 1){
                    this.infos = data.info
                  }else{
                    this.infos = []
                  }
                }
              }
              axiosRequest(conf)
            },
						pageSizeChangeFn(val){
            	this.page_size = val
            	this.init()
						},
						currentPageChangeFn(val){
							this.page = val
							this.init()
						},
						// 
						asrSound(row){
							this.$router.push({path:'asrSoundScheme/'+row.id})
						},
            // 
            sound(row){
              this.$router.push({path:"sound/"+row.sound_id})
            },
            // 
            tree(index,row){
               this.$router.push({path:"templateTree/"+row.id})
            }
		}
	}

</script>
<style lang="scss" scoped>
.TableList .el-button{padding:8px 12px;}
.TableList .el-button+.el-button{margin-left:0}
</style>


